var curry = require('../core/curry');

var isArray = require('../core/isArray');

var isEmpty = require('../core/isEmpty');

var isInteger = require('../core/isInteger');

var isDefined = require('../core/isDefined');

var isNil = require('../core/isNil');

var isString = require('../core/isString'); // propPathOr : a -> [ String | Integer ] -> b -> c


function propPathOr(def, keys, target) {
  if (!isArray(keys)) {
    throw new TypeError('propPathOr: Array of Non-empty Strings or Integers required for second argument');
  }

  if (isNil(target)) {
    return def;
  }

  var value = target;

  for (var i = 0; i < keys.length; i++) {
    var key = keys[i];

    if (!(isString(key) && !isEmpty(key) || isInteger(key))) {
      throw new TypeError('propPathOr: Array of Non-empty Strings or Integers required for second argument');
    }

    if (isNil(value)) {
      return def;
    }

    value = value[key];

    if (!isDefined(value)) {
      return def;
    }
  }

  return value;
}

module.exports = curry(propPathOr);